* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: serif;
}

body {
  color: #f1ffff;
  background-color: #d4d4d4;
}

h1 {
  font-family: "Orbitron", serif;
  height: 2rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: right;
  padding-right: 2rem;
  color: #666;
}

button {
  // width: 3rem;
  height: 2.5rem;
  border-radius: 10px;
  border-color: black;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  background-color: #ababab;

  &:hover {
    cursor: pointer;
  }
}

.calculator {
  width: 20rem;
  height: 30rem;
  max-width: 60vw;
  margin: 0 auto;
  padding: 1rem 1.3rem;
  border: 1px solid #666;
  border-radius: 10px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.5);
  background-color: #005b60;

  &__screen {
    width: 100%;
    height: 22rem;
    border: 1px solid white;
    height: 6rem;
    margin: 0 auto;
    margin-bottom: 1rem;
    padding: 3rem 2rem 1rem;
    text-align: right;
    font-family: "Orbitron", serif;
  }

  &__buttons {
    height: 22rem;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;

    &-numbers {
      width: 75%;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(5, 1fr);
      gap: 0.5rem 0.5rem;
      margin-right: 0.5rem;

      & #zero {
        grid-area: 5/1/6/3;
      }
    }

    &-signs {
      // margin-left: 0.5rem;
      width: 22%;
      display: grid;
      row-gap: 0.5rem;
      grid-template-columns: 1fr;
      /* position: relative; */
    }
  }
}

